.notify-modal {
  background: rgba(0, 0, 0, 0.25);
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center; }
  .notify-modal .content {
    background: white;
    border-radius: 20px;
    padding: 1rem;
    width: 700px;
    height: auto;
    text-align: center; }
    @media (max-width: 679px) {
      .notify-modal .content {
        width: 100%;
        padding: 0; } }
    .notify-modal .content .notifications {
      cursor: pointer;
      height: 500px;
      border-radius: 20px;
      background: #e9ecef;
      margin-bottom: 20px;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; }
    .notify-modal .content .close {
      outline: none;
      background: #339af0;
      color: white;
      border: none;
      font-weight: 700;
      font-size: 1rem;
      width: 100px;
      height: 30px;
      box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
      cursor: pointer; }
      .notify-modal .content .close:hover {
        background: #1c7ed6; }

.each-noti {
  font-size: 1.3rem;
  font-weight: 800;
  width: 90%;
  display: flex;
  justify-content: space-between;
  margin: 5px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 25%; }
  .each-noti:hover {
    background: #ced4da; }
  @media (max-width: 679px) {
    .each-noti {
      font-size: 0.9rem;
      width: 100%; } }
  .each-noti .word {
    height: 40px;
    display: flex;
    align-items: center;
    text-align: center; }
    .each-noti .word .nickname {
      color: #228be6; }
  .each-noti .img {
    width: 45px;
    height: 45px;
    object-fit: contain;
    border-radius: 50%; }
  .each-noti .postpic {
    width: 40px;
    height: 40px;
    object-fit: contain; }
