.header {
  background: #343a40;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 80px;
  width: 100%;
  color: white;
  font-weight: 600;
  font-size: 2rem;
  box-shadow: 5px 5px 39px -9px #000000; }
  @media (max-width: 329px) {
    .header {
      font-size: 1.4rem; } }
  @media (max-width: 679px) {
    .header .search-container {
      display: none; } }
  .header .search {
    height: 40px;
    width: 300px;
    border: white;
    border-radius: 3%;
    margin: 10px;
    background: #495057;
    color: white;
    text-align: center;
    font-weight: 700;
    font-size: 1.5rem; }
    @media (max-width: 679px) {
      .header .search {
        width: 200px; } }
    .header .search:focus {
      outline: none;
      -webkit-transition: 0.3s;
      transition: 0.3s;
      background: #868e96; }
  .header .right-part {
    width: 180px;
    display: flex;
    align-items: center;
    justify-content: space-around; }
    @media (max-width: 329px) {
      .header .right-part {
        width: 100px;
        justify-content: space-between; } }
    .header .right-part .button-icon {
      display: flex;
      justify-content: center;
      align-items: center; }
      .header .right-part .button-icon .dot {
        width: 10px;
        height: 10px;
        background: red;
        position: absolute;
        border-radius: 50%;
        margin-top: 24px;
        margin-left: 20px; }
        @media (max-width: 499px) {
          .header .right-part .button-icon .dot {
            margin-top: 10px;
            margin-left: 10px; } }
      .header .right-part .button-icon .imgs {
        width: 50px;
        height: 50px;
        padding: 5px;
        height: auto;
        -webkit-transition: 0.3s;
        transition: 0.3s;
        border-radius: 10px; }
        .header .right-part .button-icon .imgs:hover {
          background: #868e96; }
        @media (max-width: 499px) {
          .header .right-part .button-icon .imgs {
            width: 40px;
            height: 40px; } }
        @media (max-width: 329px) {
          .header .right-part .button-icon .imgs {
            width: 25px;
            height: 25px; } }
      @media (max-width: 329px) {
        .header .right-part .button-icon {
          height: 30px;
          width: 30px; } }
